@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Raleway', sans-serif}#navbar-style{display:-webkit-flex;display:flex;padding-left:1%;padding-top:1%}#navbar-style img{width:45px}#first-page{height:calc(90vh);padding-top:3%;overflow:auto}#first-page h1{color:aquamarine}.carousel .thumbs-wrapper{display:none}#contact-info-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}#contact-info-container h2{text-decoration:underline}#contact-info-container #contact-info{margin:1% 1% 1% 1%}#contact-info-container #contact-info p{font-size:large}#contact-info-container #contact-mid{margin:0% 5% 0% 5%;padding:0% 5% 0% 5%;border-left:1px solid white;border-right:1px solid white}#contact-info-container #icon-size{height:10%;width:10%}#contact-info-container #btn-style{cursor:pointer;background:transparent;border:0}#btn-style2{width:200px;border:5px solid #000;border-radius:15px;transition:all 0.5s ease-in-out;cursor:pointer}#btn-style2:hover{background-color:#8f8e8e}

#aboutme-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}#aboutme-image{margin-top:5%;max-height:170px;border-radius:50%}#aboutme-intro{margin-left:4%;text-align:left}#aboutme-intro h1{font-size:23px;color:yellow}#aboutme-intro b{color:yellow}#aboutme-intro p{font-size:20px}

#skill-container{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}#skill-container h1{color:aquamarine;text-decoration:underline}#skill-container img{max-width:50px;border-radius:25%}

#projectsContainer{height:calc(90vh);width:calc(95vw);font-size:large;box-sizing:border-box}blockquote{margin-top:10px;margin-bottom:10px;margin-left:50px;padding-left:15px;border-left:3px solid #ccc}blockquote::before{font-size:4em;line-height:0.1em;margin-right:0.25em;vertical-align:-0.4em;content:'\201C'}blockquote::after{content:'\201D';line-height:0.1em;margin-left:0.25em;vertical-align:-0.5em;font-size:4em}#cardContainer{height:85%;width:85%;margin-left:10%;background-color:#202020;box-shadow:5px 5px 5px #202020;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}#cardContainer h1{text-decoration:underline}@media (max-width: 425px){#cardContainer{-webkit-flex-direction:column;flex-direction:column;overflow-y:scroll;font-size:10px;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}}#cardContainer #card{width:33%;margin:1% 1% 1% 1%;background-color:#1c1c1c;box-shadow:5px 5px 5px #1c1c1c}#cardContainer #card p{margin-left:10%;margin-right:10%}#cardContainer #card #icon-grid{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}#cardContainer #card #involved-Icon{max-width:50px;border-radius:25%;transition:-webkit-transform 0.1s ease-in;transition:transform 0.1s ease-in;transition:transform 0.1s ease-in, -webkit-transform 0.1s ease-in}#cardContainer #card #involved-Icon:hover{-webkit-transform:translateY(-10px);transform:translateY(-10px)}#cardContainer #card #gallery-grid{display:grid;grid-gap:2rem}#cardContainer #card #gallery-grid img{max-width:50%;max-height:100%}#cardContainer #card #image1:hover{cursor:pointer;-webkit-transform:scale(1.2);transform:scale(1.2);transition:all 0.5s ease-in-out}#cardContainer #card #image2:hover{cursor:pointer;-webkit-transform:scale(2.2);transform:scale(2.2);transition:all 0.3s ease-out}@media (max-width: 425px){#cardContainer #card{width:90%}}

#card-container{font-size:20px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin:0 25% 0 25%}#inner-tech-card p{font-size:20px}#inner-tech-card h1{font-size:22px;color:aquamarine;text-decoration:underline}

